import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { MainService } from '../../../main.service';
import { Patient } from '../../../patient/master/patient';

@Injectable({
  providedIn: 'root'
})
export class ClinicalCoordinatorNotesService {

private baseUrl = "";

  constructor(private http: HttpClient,private mainService: MainService	
	) {
		this.baseUrl = this.mainService.getBaseUrl();  }
	private patient : Patient = null;
	
	public getPatient(): Patient{
		return this.patient;
	}
	
	public setPatient(patient : Patient) : void{
		this.patient = patient;
	}
	
	getClinicalCoordinatorNotesList():any{
		return this.http.get(`${this.baseUrl}/clinicalCoordinatorNotes/get`)
	}
	getClinicalCoordinatorNotesByPatientId(patientId ):Observable<any>{
		return this.http.get(`${this.baseUrl}/clinicalCoordinatorNotes/getByPatientId/${patientId}`);
	}
	createClinicalCoordinatorNotes(ccNotes): Observable<any>{
		return this.http.post(`${this.baseUrl}/clinicalCoordinatorNotes/add`,ccNotes)
	}
	updateClinicalCoordinatorNotes(ccNotes): Observable<any>{
		return this.http.post(`${this.baseUrl}/clinicalCoordinatorNotes/update`,ccNotes)
	}
	
	
}